
const CreateAnchorService = (header) => {

    header += ' ';
    const anchor = header.substring(0, 50)
        .toLowerCase()
        .replace(/\u00a0/g, '-')
        .replace(/[ '`]/g, '-')
        .replace(/[#?\\/()!"~+$<>=;*.&{}|,]/g, '')
        .replace(/(\[a\])|(\[\/a\])|(\[href\][A-Za-z0-9\-_.:/]+\[\/href\])|(\[text\])|(\[\/text\])|(\[strong\])|(\[\/strong\])|(\[i\])|(\[\/i\])|(\[s\])|(\[\/s\])/g, '')
        .replace(/--+/g, '-')
        .replace(/[]/g, 'a')
        .replace(/[]/g, 'e')
        .replace(/[]/g, 'i')
        .replace(/[]/g, 'o')
        .replace(/[]/g, 'c')
        .replace(/[]/g, 'u');

    return anchor.substring(0, anchor.lastIndexOf('-'));    // Pas de mots coups  la fin
};

export default CreateAnchorService